import React, { Component, Fragment } from "react";
import Link from 'react-router-dom/Link';
import PropTypes from "prop-types";
import dayjs from 'dayjs';
import MyButton from '../../util/MyButton';
import EditDetails from '../profile/EditDetails'

// Redux
import { connect } from "react-redux";
import { logOutUser, uploadImage } from '../../redux/actions/userActions'

// Material-UI
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "@material-ui/core/Button";
import MuiLink from "@material-ui/core/Link";
import Typography from "@material-ui/core/Typography";
import CircularProgress from "@material-ui/core/CircularProgress";
import IconButton from "@material-ui/core/IconButton"
import EditIcon from "@material-ui/icons/Edit"
import Paper from "@material-ui/core/Paper";
import Tooltip from "@material-ui/core/Tooltip";
import KeyboardReturn from "@material-ui/icons/KeyboardReturn"

// icons
import LocationOn from '@material-ui/icons/LocationOn';
import CalendarToday from '@material-ui/icons/CalendarToday';



const styles = theme => ({
        paper: {
          backgroundColor: theme.palette.primary.main,  
          padding: 20,
          display: "flex",
          flexDirection: "column",
          alignItems: "center"
        },
        profile: {
          '& .image-wrapper': {
            textAlign: 'center',
            position: 'relative',
            '& button': {
              position: 'absolute',
              top: '80%',
              left: '70%'
            }
          },
          '& .profile-image': {
            width: 200,
            height: 200,
            objectFit: 'cover',
            maxWidth: '100%',
            borderRadius: '50%'
          },
          '& .profile-details': {
            textAlign: 'center',
            '& span, svg': {
              verticalAlign: 'middle'
            },
            '& a': {
              color: theme.palette.secondary.light
            }
          },
          '& hr': {
            border: 'none',
            margin: '0 0 10px 0'
          },
          '& svg.button': {
            '&:hover': {
              cursor: 'pointer'
            }
          }
        },
        buttons: {
          textAlign: 'center',
          '& a': {
            margin: '20px 10px'
          }
        },
        typo: {
            color: theme.palette.secondary.light
        },
        handle: {
            color: theme.palette.secondary.light
        },
        editImage: {
          color: theme.palette.secondary.light
        }
});

class Profile extends Component {
  handleImageChange = (event) => {
    const image = event.target.files[0];
    const formData = new FormData();
    formData.append('image', image, image.name);
    this.props.uploadImage(formData);
  };

  handleEditImage = () => {
    const fileInput = document.getElementById('imageInput');
    fileInput.click();
  };

  handleLogout = () => {
    this.props.logOutUser();
  }

  render() {
    const {
      classes,
      user: {
        credentials: { handle, createdAt, imageUrl, bio, location },
        loading,
        authenticated
      }
    } = this.props;

    let profileMarkUp = !loading ? (authenticated ? (
        <Paper className={classes.paper}>
            <div className={classes.profile}>
                <div className="image-wrapper">
                    <img src={imageUrl} alt="profile" className="profile-image"/>
                    <input
                        type="file"
                        id="imageInput"
                        hidden="hidden"
                        onChange={this.handleImageChange}
                      />
                      <MyButton
                        tip="Edit Profile Picture"
                        onClick={this.handleEditImage}
                        btnClassName="button"
                      >
                        <EditIcon className={classes.editImage} />
                      </MyButton>
                </div>
                <hr/>                                                 
                <div className="profile-details">
                    <MuiLink component={Link} to={`/users/${handle}`} className={classes.handle} variant="h5">
                        @{handle}
                    </MuiLink>
                    <hr/>
                    {bio && <Typography className={classes.typo} variant="h4">{bio}</Typography>}
                    <hr/>
                    {location && (
                        <Fragment>
                            <LocationOn /> <span>{location}</span>
                            <hr/>
                        </Fragment>
                    )}
                    <CalendarToday /> {' '}
                    <span>Joined {dayjs(createdAt).format('MMM YYYY')}</span>
                </div>
                <Tooltip title="Logout" placement="top">
                  <IconButton onClick={this.handleLogout}>
                    <KeyboardReturn className={classes.editImage} />
                  </IconButton>
                </Tooltip>
                <EditDetails />
            </div>
        </Paper>
    ) : (
        <Paper className={classes.paper}>
            <Typography className={classes.typo} variant="body2" align="center">Uh, Oh!! Where'd you go? Please Login Again!</Typography>
            <div className={classes.buttons}>
                <Button variant="contained" color="primary" component={Link} to="/login">
                    Login
                </Button>
                <Button variant="contained" color="secondary" component={Link} to="/signup">
                    Sign Up
                </Button>
            </div>
        </Paper>
    )) : (<CircularProgress size={30} className={classes.login}/>)

    return profileMarkUp;
  }
}

const mapStateToProps = state => ({
  user: state.user
});

const mapActionsToProps = {
  logOutUser,
  uploadImage
};

Profile.propTypes = {
  logOutUser: PropTypes.func.isRequired,
  uploadImage: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired
};

export default connect(mapStateToProps, mapActionsToProps)(withStyles(styles)(Profile));
