// Theme
export default {

    palette: {
      primary: {
        light: "#fff",
        main: "#1E1E1E",
        dark: "#000",
        contrastText: "#fff"
      },
      secondary: {
        light: "#fff",
        main: "#5E0000",
        contrastText: "rgba(204, 204, 204, 0.685)"
      },
    },
      typography: {
        useNextVarients: true
      },   grid: {
        textAlign: "center",
        display: "flex",
        justifyContent: "center"
      },
      form: {
        display: "flex",
        flexDirection: "column"
      },
      icon: {
        maxWidth: 50,
        margin: "50px auto 10px auto"
      },
      pageTitle: {
        margin: "10px auto 35px auto"
      },
      textField: {
        padding: "0px 0px 10px 0px"
      },
      button: {
        marginTop: "30px",
        position: "relative",
      },
      customError: {
        color: "red",
        fontSize: "0.8rem",
        marginTop: "10px"
      },
      login: {
        position: "absolute",
        color: "white"
      },
      small: {
        color: "#1E1E1E",
        fontWeight: "bold" 
      }, 
      loadingContainer: {
        width: "100%",
        display: 'flex',
        justiftContent: "center"
      },
      loading: {
        display: "flex"
      },
      
}